<template>
  <div class="spot-image">
    <figure class="spot-figure" v-if="image">
      <b-img class="spot-figure-image" v-bind:src=image.url fluid-grow alt="スポット画像"></b-img>
      <figcaption v-if="image.caption" class="spot-figure-caption">
          {{ image.caption }}
          <small v-if="image.credit">(Credit: {{ image.credit }})</small>
      </figcaption>
    </figure>
    <!--
      <div id="viewer"></div>
      <script>
        tifyOptions = {
          container: '#viewer',
          immediateRender: false,
          init: true,
          language: 'de',
          manifest: 'https://www.dl.ndl.go.jp/api/iiif/2558314/manifest.json',
          stylesheet: '../assets/my-very-own-tify-styles.css',
          title: null,
        }
      </script>
      <script src="../assets/tify.js"></script>
    -->
  </div>
</template>

<script>
export default {
  props: {
    image: Object
  }
}
</script>



